.background {
    display: block;
    position: fixed;
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: -1;
    background-repeat: no-repeat;
    background-position: center top;
    background-attachment: fixed;
    background-size: cover;
    /*background-image: url("data:%3C;base64,");*/
    background-color: rgb(180, 150, 230);
    background-image:
        linear-gradient(
            180deg,
            rgba(0, 0, 5, 0) 0%,
            rgba(255, 150, 0, 0.8) 80%,
            rgba(255, 100, 0, 1) 90%,
            rgba(255, 100, 0, 1) 100%
        ),
        radial-gradient(
            45vh at 50% 80%,
            rgba(255, 0, 0, 1.0) 0%,
            rgba(255, 150, 0, 0.9) 60%,
            rgba(0, 0, 0, 0) 100%
        )
    ;
}

.background_horizon {
    background-color: transparent;
    background-image:
        linear-gradient(
            180deg,
            rgba(10, 0, 50, 0.0) 0%,
            rgba(10, 0, 50, 0.0) 85%,
            rgba(10, 0, 50, 0.7) 87%,
            rgba(10, 0, 50, 1) 95%,
            rgba(10, 0, 25, 1) 100%
        )
    ;
}


.text_content {
    margin-bottom: 30em;
    /*padding: 1em;*/
    z-index: 1;
}


